html[data-theme="light"] {
    // --bg--url--desktop : url("todo-list\src\images\bg-desktop-light.jpg");
    --add--todo : #9495A5;
    --bg--body: #F2F2F2;
    --bg--todo: #FFFFFF;
    --border: #E3E4F1;
    --input--base: #494C6B;
   --input--edit : #fcfcfc;
   --input--checked: #D1D2DA;
   --button--option: #9495A5;
   --button--option--hover: black;
}

html[data-theme="dark"]{
    // --bg--url--desktop : url('todo-list\src\images\bg-desktop-dark.jpg');
    --add--todo : #767992;
    --bg--body: #171823;
    --bg--todo: #25273D;
    --border : #393A4B;
    --input--base : #C8CBE7;
    --input--edit : #282b41;
    --input--checked: #4D5067;
    --button--option: #5B5E7E;
    --button--option--hover: #767992;
}